@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&dispaly=swap);
/* Variables */
:root {
	--mainColor: #e02f6b;
	--blue: #0000ff;
	--blueDark: #18293c;
	--orange: #ffa500;
	--greenYellow: #cddc39;
	--pinkLight: #efa2b4;
	--cyanLight: #aef1ee;
	--white: #ffffff;
	--whiteAphla40: rgba(255, 255, 255, 0.4);
	--whiteAphla25: rgba(255, 255, 255, 0.25);
	--backdropFilterBlur: blur(5px);
}

/* Global Settings */

* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	outline: none;
}

::before,
::after {
	box-sizing: border-box;
}

body {
	margin: 0;
	/* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif; */
	font-family: "Poppins", sans-serif;
	font-weight: 300;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	/* min-height: 100vh; */
	background-image: linear-gradient(
		to bottom right,
		#efa2b4,
		#aef1ee);
	background-image: linear-gradient(
		to bottom right,
		var(--pinkLight),
		var(--cyanLight)
	);
	background-attachment: fixed;
	font-size: 16px;
	color: #18293c;
	color: var(--blueDark);
	line-height: 1.5;
	overflow-x: hidden;
	-webkit-tap-highlight-color: transparent;
	padding: 32px 15px;

}

body::before {
	content: "";
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	height: 100%;
	width: 100%;
	background-color: #cddc39;
	background-color: var(--greenYellow);
	z-index: -1;
	opacity: 0.12;
}

.glass-page {
	background-color: rgba(255, 255, 255, 0.25);
	background-color: var(--whiteAphla25);
	border: 1px solid rgba(255, 255, 255, 0.4);
	border: 1px solid var(--whiteAphla40);
	min-height: calc(100vh - 70px);
	border-radius: 30px;
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
	-webkit-backdrop-filter: var(--backdropFilterBlur);
	        backdrop-filter: var(--backdropFilterBlur);
}

h1,
h2 {
	font-weight: 600;
}

h3,
h4,
h5,
h6 {
	font-weight: 500;
}

ul {
	list-style: none;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
	width: 5px;
}

::-webkit-scrollbar-track {
	background-color: #ffffff;
	background-color: var(--white);
}

::-webkit-scrollbar-thumb {
	background-color: #e02f6b;
	background-color: var(--mainColor);
}

.overlay {
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	background-color: red;
	z-index: 200;
	opacity: 0.5;
  }
